* {
  font-family: "Poppins", sans-serif;
}

.navigation-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 2em 6em;
  max-width: 1400px;
}

.navigation-bar h5 {
  font-size: 20px;
}

.navigation-bar h2 {
  font-size: 20px;
}

.shots {
  display: flex;
  flex-wrap: wrap;
  margin: 5em auto 5em;
  max-width: 1400px;
  align-items: center;
}

.shot img {
  width: 665px;
  max-height: 375px;
  display: block;

  transition: opacity 0.2s ease-in-out;
  border-radius: 5px;
  margin: 1em;
  box-shadow: 0 0 15px -1px rgba(0, 0, 0, 0.1);
}

.shot img:hover {
  -webkit-transform: scale(0.97);
  transform: scale(0.97);
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
  cursor: pointer;
}

.intro {
  margin-top: 3em;
  letter-spacing: 1px;
}

.intro h1 {
  font-weight: 500;
  font-size: 35x;
}

.logos {
  opacity: 1;
  -webkit-transform: translateY(40px);
  transform: translateY(40px);
  -webkit-transition: 0.7s all ease-in-out;
  transition: 0.7s all ease-in-out;
  max-width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin: 7em auto 5em;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  max-width: 1400px;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-shadow: 0 0 15px -1px rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 15px -1px rgba(0, 0, 0, 0.1);
}

.logos img {
  height: 250px;
  width: 250px;
  margin: 2.5em 1.5em;
  border-radius: 30px;
}

.logos img:hover {
  -webkit-transform: scale(0.97);
  transform: scale(0.97);
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
  cursor: pointer;
}

.btnContact {

  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  margin: 4em 0
}

.btnContact a {
  text-decoration: none;
  color: white;
}

.btnContact a:hover {
  text-decoration: none;
  color: white;
}

.btnContact button {
  width: 220px;
  padding: 1.5em 0;
  background-color: #FD3A51;
  border: none;
  color: white;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 16px;
  letter-spacing: 2px;
  display: flex;
  justify-content: center;
  border-radius: 1000px;

}

button:hover {
  background-color: #f37d8b;
  cursor: pointer;
}

button i {
  padding-right: 10px;
  font-size: 24px;
}

.contact h2 {
  margin: 9em 0;
}

@media only screen and (max-width: 768px) {
  .navigation-bar {
    margin: 0 5%;
    width: 90%;
    padding-top: 20px;
  }

  .navigation-bar h2 {
    font-size: 20px;
  }

  .navigation-bar i {
    font-size: 25px;
  }

  .intro h1 {
    font-size: 2.3rem;
  }

  .shots {
    margin: 4em 0;
    position: relative;
  }

  .shot img {
    width: 90%;
    margin: 10px auto;
  }
}

@media only screen and (max-width: 1393px) {
  .shots {
    flex-direction: column;
  }
}